.Works{
    display: inline-block;
    width: 350px;
    height: 250px;
    position: relative;
    margin: 30px 10px;
}

.container{
    width: 100%;
    height: 100%;
    background-color: rgba(255, 255, 255, 0.3);
    opacity: 0;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    transition: 0.7s;
    display: flex;
    justify-content: center;
    align-items: center;
}

.container:hover {
    opacity: 1;
    box-shadow: 0 0 10px rgba(129, 129, 129, 1);
}

img {
    width: 100%;
    height: auto;
}

h3 {
    color: rgb(255, 92, 92)
}
